<template>
    <div class='container-form'>
        <form method='POST' @submit="sendForm" >
            <div>
                <div class='frist' v-if='isVisible == false'>
                    <input  type='text' onChange='' class='input-form' v-model='nome' placeholder='nome'/>
                    <input  type='text' onChange='' class='input-form' v-model='usuario' placeholder='usuario'/>
                    <input  type='text' onChange='' class='input-form' v-model='senha' placeholder='senha'/>
                    <input  type='text' onChange='' class='input-form' v-model='email' placeholder='email'/>
                    <input  type='text' onChange='' class='input-form' v-model='telefone' placeholder='telefone'/>
                    <input  type='text' onChange='' class='input-form' v-model='celular' placeholder='celular'/>
                    <button class='input-button' @click='changeHandler' >proximo</button>
                </div>
                <div class='second' v-if='isVisible'>
                    <input  type='text' onChange='' class='input-form' @change='getEndereco' v-model='cpf' placeholder='cpf'/>
                    <input  type='date' onChange='' class='input-form' v-model='dataNascimento' placeholder='dataNascimento'/>
                    <input  type='text' onChange='' class='input-form' v-model='cep' placeholder='cep'/>
                    <input  type='text' onChange='' class='input-form' v-model='rua' placeholder='endereco'/>
                    <input  type='text' onChange='' class='input-form' v-model='numero' placeholder='numero'/>
                    <input  type='text' onChange='' class='input-form' v-model='bairro' placeholder='bairro'/>
                    <input  type='text' onChange='' class='input-form' v-model='estado' placeholder='estado'/>
                    <input  type='text' onChange='' class='input-form' v-model='cidade' placeholder='cidade'/>
                    <input  type='text' onChange='' class='input-form' v-model='complemento' placeholder='complento'/>
                    <input type='submit' class='input-button' value='enviar'/>
                </div>
            </div>
        </form>
    </div>
        
</template>

<script>
import api from '../../api.js'
export default {
    data(){
        return{
            nome: '',
            sexo: '',
            usuario: '',
            senha: '',
            cpf: '',
            dataNascimento: '',
            estado:'',
            cidade:'', 
            bairro:'', 
            rua: '',
            numero: '',
            cep: '',
            complemento: '',
            email:'',
            telefone:'', 
            celular: '',
            isVisible:false,
            
        }
    },

    methods:{
        changeHandler(){
            this.isVisible = !this.isVisible
        },
        sendForm(e){
            e.preventDefault();
            const body = JSON.stringify({
                nome: this.nome,
                sexo: this.sexo,
                usuario: this.usuario,
                senha: this.senha,
                cpf: this.cpf,
                dataNascimento: this.dataNascimento,
                estado:this.estado,
                cidade:this.cidade, 
                bairro:this.bairro, 
                rua: this.rua,
                numero: this.numero,
                cep: this.cep,
                complemento: this.complemento,
                email:this.email,
                telefone: this.telefone, 
                celular: this.celular,
            }) 
            api('http://localhost:4000/addCadastro','POST',body)
            // .then((res)=>res.json())
            // .then((res)=>{
            //     console.log(resp)
            // })
        },
    },
    watch:{
        cep:function(val){
            if(val.split("").length == 9){
                
                var cepRemoveMask = val.replace(/(\d{5})-(\d{3})/,'$1$2')
                api(`https://ws.apicep.com/cep/${cepRemoveMask}.json`,'GET')
                .then((resp)=>resp.json())
                .then((resp)=>{
                    setTimeout(()=>{
                        this.rua = resp.address;
                        this.bairro = resp.district;
                        this.cidade = resp.city;
                        this.estado = resp.city;
                    },300)    
                })  
            }
            if(val.split("").length <= 8){
                this.logradouro = null
                this.bairro = null
                this.cidade = null
                this.estado = null
            }
            let cepMask = this.cep
                .replace(/\D/,"")
                .replace(/(\d{5})(\d{3})/,'$1-$2')
            this.cep = cepMask;
        },
    }
}
</script>
